      h2 3.3 Momentos de verdad
    p Conozca algunas definiciones:
    p <strong>Segn Jan Carlzon.</strong> Como afirma el autor en su libro El momento de la verdad, una empresa orientada al producto puede ser reemplazada fcilmente por un nuevo competidor que ofrece un producto similar/igual a un costo menor. (Carlzon, 1996)
    .row.mt-4
      .col-md-4(data-aos="zoom-out-right")
        img(src='@/assets/curso/tema3/img5.png' alt='AvatarTop')
      .col-lg-8
        <br><br><br>
        p El autor Jan Carlzon, en el libro <strong>Momento de la verdad</strong>, refiere una forma distinta de gestionar organizaciones, Jan Carlzon invierte la pirmide jerrquica que tradicionalmente establecen las organizaciones y en la cual los directivos y ejecutivos ocupan la cima y quienes tienen contacto directo con los clientes ocupan la base piramidal.

        p Debido a los cambios y crecimiento de los mercados y al incremento de la competencia, es primordial que las organizaciones se concentren en el cliente y no en el producto; de esta manera, pueden disear productos dirigidos a satisfacer las necesidades de los clientes. Una empresa u organizacin orientada al cliente genera mayores oportunidades para fidelizar al cliente y permite la generacin de ingresos para la sostenibilidad y permanencia en el mercado.   (Carlzon, 1996)
    p Los momentos de verdad, en s, no generan una valoracin positiva o negativa, lo que genera esta valoracin es el manejo que se le d al acercamiento con el cliente y que convierta dicho encuentro en una experiencia positiva. La organizacin debe anticiparse a la identificacin y a la generacin de posibles momentos de verdad, y dar el manejo, logrando experiencias positivas al cliente.  
    .row.mt-4
      .col-lg-12.p-4
        .cajon.color-primario.p-4.mb-4(data-aos="zoom-out-right")
          .col-lg-11.p-4
            p <strong>Reflexione sobre lo siguiente:</strong>
            p Juan debe enviar un sobre con documentos desde la ciudad de Medelln a la ciudad de Bogot. Los documentos deben estar en manos del destinatario antes de 24 horas. Juan se desplaza a la empresa <strong>Entrega Inmediata</strong>, que es una empresa de servicios postales y paquetera ubicada cerca de su lugar de trabajo, pero, al llegar a las oficinas de la empresa, encuentra que no tienen parqueadero disponible, los 4 parqueaderos asignados a sus clientes ya estn ocupados y no puede ocupar los parqueaderos disponibles, el servicio de seguridad no se lo permite. Se ve en la obligacin de desplazarse a la empresa <strong>Entrega Hora Exacta</strong> y, pese a que tampoco haba parqueadero disponible asignado a la empresa, el servicio de seguridad le permiti ubicar el vehculo por un periodo de tiempo en otro parqueadero. Al llegar a la empresa, haba varios clientes esperando atencin, pero la empresa tiene habilitada una ventanilla de recepcin de envos catalogados como urgentes; este servicio tiene un valor un poco ms alto que el habitual, pero garantiza la entrega oportuna. Juan tom este servicio y agradeci la oportunidad. <strong>Cree usted que Juan obtuvo momentos de verdad?.</strong>    

    p <strong>El portafolio de servicios es una herramienta de la que dispone la empresa para dar a conocer los productos o servicios. </strong> Se puede disponer de esta herramienta en formato impreso o en digital.

    p En el caso de las empresas de envos postales y mercancas, los portafolios de servicios contienen informacin como:
    .row.mt-4.justify-content-center.mt-5(data-aos="zoom-out-down")
