    <br>
    TabsB.color-acento-contenido.mb-5.mt-4(data-aos="zoom-out-down")
      .py-4.py-md-5(titulo="Trazabilidad" :icono="require('@/assets/curso/tema3/ico2.svg')")
        p <strong>Trazabilidad</strong>

        .row
          .col-md-7.mb-4.mb-md-0
            p Las empresas de servicio o de produccin deben estructurar procesos de trazabilidad, esto les permite hacer un rastreo que va desde la compra de materias primas e insumos, hasta la produccin y la entrega; tiene como objetivo controlar todos y cada uno de los procesos de produccin, asegurar la calidad y el ptimo estado de los productos, desde el origen hasta entregarlo al consumidor final. En las empresas de servicios postales, la trazabilidad va desde la recepcin del paquete o mercanca por parte del remitente, hasta la entrega al destinatario.
            p Se tienen dos tipos de trazabilidad: la trazabilidad interna y la trazabilidad externa. Cuando se habla de la <STRONG>trazabilidad interna,</strong> se hace referencia al registro que deja un producto cuando avanza por los procesos internos de la organizacin, lo que incluye las mquinas utilizadas, turnos, almacenajes, disposicin para carga y dems procesos. Por consiguiente, la <strong>trazabilidad externa</strong> son los procesos implementados para obtener informacin entre la cadena de abastecimiento o suministro.
        
          .col-md-5
            figure
              img(src='@/assets/curso/tema3/img7.png', alt='Texto que describa la imagen')
      .py-4.py-md-5(titulo="Canales de distribucin" :icono="require('@/assets/curso/tema3/ico3.svg')")
        .row
          .col-md-7.mb-4.mb-md-0
            p <strong>Canales de distribucin</strong>
            p Ahora, como elemento integral del proceso de servicios postales y transporte de mercanca, se abordan los canales de distribucin, que representan la  estructura organizacional que presta un servicio desde un lugar de origen, donde se encuentra el remitente, hasta un lugar destino, donde se encuentra el destinatario. 
            p Los canales de distribucin satisfacen necesidades especficas de los clientes en el traslado de envos postales, paquetes y mercancas; para ello, realizan un proceso interno de distribucin fsica. La distribucin fsica es la manera en que se trasladan o mueven los productos por los canales de distribucin, que son: almacenamiento, empaque, manejo de materiales, procesamiento de pedidos, transporte y control de inventarios.
        
          .col-md-5
