    p Las solicitudes que las organizaciones o personas realizan a las empresas de servicios postales y transporte de mercancas son las correspondientes al traslado de paquetes y de mercancas a travs del uso de los recursos que la empresa tiene disponibles para la prestacin del servicio y que este se haga dentro de parmetros de confiabilidad. Por lo tanto, se deben realizar controles en el proceso; estos controles los aplican tanto la empresa remitente como la prestadora del servicio. Esto permite conocer algunos de los controles disponibles:    

