            p.mb-0 Las entregas de los envos postales o de mercancas se hacen segn el protocolo y los tiempos establecidos para ello. Sin embargo, las empresas ofrecen algunos servicios que dan priorizacin, uno de ellos se llama servicio de mensajera expresa, es un servicio postal urgente, la prestacin de este servicio exige la aplicacin de unas condiciones diferentes y especiales en la recepcin, recoleccin, clasificacin, transporte y entrega; es importante considerar que este servicio es para envos postales o mercancas de hasta mximo 5 kilos. <strong>(Superintendencia de Industria y Comercio, s. f.)</strong>

    .row.mt-4.justify-content-start.align-items-center            
